.sneaky-item::after {
  position: absolute;
  left: 6px;
  width: 1px;
  background: #eceeef;
  height: 100%;
  top: -48px;
  content: "";
}

.sneaky-item::before {
  position: absolute;
  left: 6px;
  width: 1px;
  background: #eceeef;
  height: 100%;
  bottom: -20px;
  content: "";
}

.sneaky-child {
  position: relative;
}

.sneaky-child::before {
  position: absolute;
  content: "";
  height: 1px;
  background: #eceeef;
  top: 45%;
  left: -19px;
  width: 100px;
}

.sneaky-from {
  position: relative;
  top: -17px;
  left: -4px;
  z-index: 100;
  color: #8b8c8c;
  font-weight: 300;
}

.sneaky-from::after {
  content: "";
  position: absolute;
  border-radius: 10px;
  background: #c2c4c5;
  width: 11px;
  height: 11px;
  left: -20px;
  top: 6px;
}

.sneaky-from-can-delete::after {
  content: "";
  /*cursor: pointer;*/
  background: white;
  /*width: 15px;*/
  /*height: 15px;*/
  /*left: -22px;*/
  /*top: 4px;*/
  /*color: white;*/
  /*text-align: center;*/
  /*font-size: 22px;*/
  /*line-height: 0.7;*/
  /*font-weight: 500;*/
}

.sneaky-edit-child,
.sneaky-add-children {
  color: #0000c8;
  position: relative;
  font-style: italic;
  margin-left: 8px;
  cursor: pointer;
  font-weight: 300;
}

.sneaky-create-new-variation {
  color: #0000c8;
  position: relative;
  font-style: italic;
  cursor: pointer;
  font-weight: 300;
  margin-left: 0;
  margin-bottom: 15px;
  margin-top: 23px;
  border-bottom: 1px solid #d9d9d9;
  padding-bottom: 10px;
  border-top: 1px solid #d9d9d9;
  padding-top: 10px;
  text-align: center;
}

.sneaky-add-line {
  color: rgb(43, 160, 43);
  font-size: 38px;
  cursor: pointer;
  display: inline-block;
  position: relative;
  left: 5px;
  top: -20px;
}

.sneaky-detatch {
  cursor: pointer;
  width: 17px;
  height: 17px;
  top: 3px;
  color: white;
  text-align: center;
  font-size: 13px;
  line-height: 1.4;
  font-weight: 500;
  position: absolute;
  left: -22px;
  z-index: 100;
  background: #c80000;
  border-radius: 30px;
}

