/* Hide the browser's default radio button */
.Style_rA_container__OnCjD input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom radio button */
.Style_rA_checkmark__376aY {
  position: absolute;
  top: 0;
  left: 0;
  height: 28px;
  width: 28px;
  background-color: #ccc;
  border-radius: 50%;
}

/* On mouse-over, add a grey background color */
.Style_rA_container__OnCjD:hover input ~ .Style_rA_checkmark__376aY {
  background-color: #999;
}

/* When the radio button is checked, add a blue background */
.Style_rA_container__OnCjD input:checked ~ .Style_rA_checkmark__376aY {
  background-color: #8db965;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.Style_rA_checkmark__376aY:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.Style_rA_container__OnCjD input:checked ~ .Style_rA_checkmark__376aY:after {
  display: block;
}

