.container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px;
  padding-bottom: 150px;
}

.small_container {
  max-width: 600px;
  margin: auto;
}

.center_container {
  min-height: 100vh;
}

.inner_container {
  /* PØLSE */
  /*max-width: 500px;*/
  width: 100vw;
}

/* COLORS */
.bgBlue {
  background-color: #006da5;
  color: white;
}

.cBlue {
  color: #006da5;
}

.bgGreen {
  background-color: #ff0000;
  color: white;
}

.cGreen {
  color: #ff0000;
}

.container section {
  margin-bottom: 50px;
}

.largerText,
.largerText p {
  font-size: 22px;
}

.smallerText,
.smallerText p {
  font-size: 16px;
}

.messageBox {
  background-color: #d4e8f4;
  padding: 25px 40px;
}

.messageBox p {
  color: #026ca0;
  margin: 0;
  padding: 0;
  font-weight: 600;
}

.responseBox {
  font-size: 16px;
  border-radius: 2px;
  display: table;
  margin: 20px 0px;
  padding: 18px 24px;
  font-weight: 500;
  line-height: 2;
}

.successMessage {
  color: #557933;
  background: #d4e5c5;
}

.errorMessage {
  background-color: #ffd1d1;
  color: #924a4a;
}

.container.reversed {
  color: white;
  background-color: #006da5;
}

.container.reversed h1,
.container.reversed h2,
.container.reversed h3,
.container.reversed h4,
.container.reversed a {
  color: white;
}

/* FORMS */
.formGroup {
  margin-bottom: 40px;
  display: block;
}

/* CHECKMARK */
.cM_container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.cM_container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.cM_checkmark {
  position: absolute;
  font-size: 28px;
  top: -8px;
  left: -2px;
  height: 25px;
  width: 25px;
  color: #ccc;
}

.cM_container:hover input ~ .cM_checkmark {
  color: #999;
}

.cM_container input:checked ~ .cM_checkmark {
  color: #8db965;
}

/* Customize the label (the container) */
.rA_container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default radio button */
.rA_container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom radio button */
.rA_checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 28px;
  width: 28px;
  background-color: #ccc;
  border-radius: 50%;
}

/* On mouse-over, add a grey background color */
.rA_container:hover input ~ .rA_checkmark {
  background-color: #999;
}

/* When the radio button is checked, add a blue background */
.rA_container input:checked ~ .rA_checkmark {
  background-color: #8db965;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.rA_checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.rA_container input:checked ~ .rA_checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.rA_container .rA_checkmark:after {
  top: 9px;
  left: 9px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: white;
}

.success_box {
  background: #36a7361c;
  padding: 17px 20px;
  border: 1px solid #36a736;
  line-height: 2;
  color: #36a736;
}

.success_box h4 {
  padding: 0;
  padding-bottom: 5px;
  margin: 0;
  color: #36a736;
  font-size: 18px;
  font-weight: 600;
}

.error_box {
  background: #c800001f;
  padding: 17px 20px;
  border: 1px solid;
  line-height: 2;
  color: #c80000;
  margin-bottom: 50px;
  word-break: break-word;
}

.skip_class {
  border: none;
  background: #37a737;
  padding: 12px 24px;
  font-size: 18px;
  color: white;
  margin-bottom: 20px;
  cursor: pointer;
  width: 100%;
  display: block;
  text-align: center;
  text-decoration: none;
}

.button_class {
  border: none;
  background: #37a737;
  padding: 12px 24px;
  font-size: 18px;
  color: white;
  margin-bottom: 20px;
  cursor: pointer;
  width: 100%;
  display: block;
  text-align: center;
  text-decoration: none;
  position: relative;
}

.button_class:hover {
  background: #2c922c;
}

.button_class i {
  font-size: 28px;
  position: absolute;
  right: 13px;
  top: 14px;
}

.error_class {
  color: #c80000 !important;
  background: #c800001f !important;
  box-shadow: 0px 0px 0px 1px inset;
}

.error_message {
  color: rgb(200, 0, 0);
  font-size: 12px;
  padding-top: 10px;
  padding-bottom: 2px;
}

