.container {
  background: white;
  padding: 20px;
  max-width: 700px;
  margin: auto;
  margin-top: 30px;
  margin-bottom: 150px;
}

.white_cover {
  background: white;
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
}


.content {
  z-index: 9999;
  position: relative;
  white-space: pre-wrap;
}
.header {}

.title_section {
  padding-bottom: 40px;
  position: relative;
}

.title_section h1 {
  margin: 0;
  font-size: 114px;
  font-weight: 100;
  position: relative;
  color: #2196F3;
  margin-left: -10px;
}

.title_section i {
  transform: rotate(12deg);
  font-size: 220px;
  color: #ffd500;
  background: #9c069c;
  width: 190px;
  height: 190px;
  border-radius: 100%;
  text-align: center;
  line-height: 0.91;
  text-shadow: -5px 5px orange;
  box-shadow: -5px 5px #5f075f;
  position: absolute;
  right: 0;
  top: 0;
}


/*.date,*/
.customer_section {
  display: inline-block;
  color: purple;
  position: relative;
  top: -14px;
}

.meta_section {
  display: flex;
  justify-content: space-around;
  margin-top: 30px;
  flex-wrap: wrap;
}

.meta_each {
  display: inline-block;
  text-align: center;
  position: relative;
  margin-bottom: 30px;
  margin-top: 15px;
}

.meta_key {
  padding-top: 15px;
  position: relative;
  text-align: center;
  color: #666;
}

.meta_val {
  text-align: center;
  color: white;
  background: #9c079c;
  border-radius: 100%;
  font-size: 105px;
  font-weight: 300;
  width: 160px;
  height: 160px;
  line-height: 1.45;
  letter-spacing: -8px;
  padding-right: 10px;
  box-shadow: -3px 3px 0 0 #5f065f;
}


.section {
  margin: 75px 0px;
}

.section h2 {
  margin: 15px 0px;
  font-weight: 500;
  font-size: 29px;
  color: #2196F3;
  margin-top: 7px;
}

.sub_header {
  color: orange;
  text-transform: uppercase;
  font-size: 14px;
}

.section_sub {
  line-height: 2.2;
  color: #666;
  font-size: 17px;
  font-weight: 300;
}

.startup_cards {
  margin-top: 30px;
}

.startup_card {
  padding: 15px;
  position: relative;
  overflow: hidden;
  background: #fcfff1;
  border-radius: 5px;
  box-shadow: 0px 2px 5px -3px rgba(0,0,0,1);
  min-height: 200px;
  margin-bottom: 20px;    
}

.startup_card_left {
  background: #009688;
  position: absolute;
  left: 0;
  top: 0;
  width: 200px;
  height: 100%;
  text-align: center;
  padding-top: 30px;
  padding-bottom: 30px;
}

.startup_card_right {
  padding-left: 220px;
}

.startup_card_header {

}

.startup_logo {
  width: 90px;
  height: 90px;
  box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.5);
  border-radius: 100%;
  overflow: hidden;
  margin: auto;
  background: white;
}

.startup_logo img {
  width: 100%;
  height: 100%;
}

.startup_name {
  font-size: 28px;
  font-weight: 300;
  padding-top: 15px;
}

.startup_text_container {
  margin: 25px 0px;
}

.startup_text_title {
  color: orange;
  font-weight: 300;
  padding-bottom: 5px;
}

.startup_text {
  line-height: 2;
  padding-right: 20px;
  color: #333;
}

.event {
  margin: 10px 10px 20px 25px;
  position: relative;
}

.event:before {
  content: '';
  width: 1px;
  background: #ddd;
  height: 100%;
  position: absolute;
  left: -20px;
}

.event_title {
  margin: 10px 0px;
}

.event_date {
  color: orange;
  font-size: 14px;
  font-weight: 300;
}

.event_description {
  color: #333;
  font-size: 14px;
  font-weight: 300;
  line-height: 2;
}

.tags {
  padding-top: 30px;
}

.tag {
  display: inline-block;
  color: white;
  background: rgba(255,255,255,0.3);
  padding: 3px 12px;
  border-radius: 3px;
  margin: 2px;
}

.weblink {
  margin-top: 6px;
}

.weblink a {
  text-decoration: none;
  background: purple;
  color: white;
  padding: 2px 8px;
  border-radius: 2px;
  font-size: 13px;  
  cursor: pointer;
}

.calendar {

}

@media screen and (max-width: 600px) {
  .title_section {
    text-align: center;    
  }

  .title_section h1 {
    margin-top: 35px;
    margin-bottom: 15px;    
  }
  .title_section i {
    position: relative;
    display: inline-block;
    text-align: center;
  }
  .startup_card_right {
    padding-left: 0px;
  }
  .startup_card_left {
    display: none;
  }  
}

.eval_thumb {
  float: right;
  font-size: 20px;
  color: white;
  /*background: #2196f3;*/
}

.eval_thumb i {
  padding: 6px 8px;
  margin: 1px;
  background: #2196f3;
  cursor: pointer;
  opacity: 0.3;
  font-size: 22px;
}






