.text_area {
  border: 0;
  outline: none;
  width: 100%;
  resize: none;
  background: #ededed;
  border-radius: 5px;
  padding: 17px 20px;
  margin-top: 20px;
  font-size: 18px;
  color: #666;
  min-height: 125px;
}


.image_div {
  margin-top: 20px;
}

.image_div img {
  width: 100%;
}

.talks_container {

}

.talk_container {
padding-top: 10px;
    padding-bottom: 10px;
    margin-top: 40px;
    margin-bottom: 10px;
    /*border-bottom: 1px dashed rgba(128,128,128,0.3);*/
}

.talk_title {
  text-transform: uppercase;
  font-weight: 600;
      color: #800080;

}

.talk_person {
color: #666;
    font-weight: 300;
    padding: 10px 0px;
}

.talker_name {

