      /*margin: 50px 0px;*/

}

.quote_container {
    margin: 35px 0px;
    position: relative;
    padding-left: 22px;
}

.quote_container:before {
    content: "";
    position: absolute;
    width: 1px;
    height: 100%;
    background: rgba(0,0,0,0.2);
    left: 0;  
}

.quote_text {
    font-style: italic;
    line-height: 2;
    color: #666;
}

.quote_person {
    font-weight: 300;
    /*color: #666;*/
    color: orange;
    font-size: 16px;
    /*padding-left: 25px;*/
    padding-top: 10px;
    font-style: italic;
}
